.feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.25em;
}
.feature.col--2x {
  grid-row-end: span 2;
}
.feature.col--3x {
  grid-row-end: span 3;
}

.feature {
  text-decoration: none;
}

.feature .feature-title {
  font-size: 18px;
  text-decoration: underline;
}
.feature .feature-details {
  display: flex;
  align-items: center;
  font-size: 14px;
  height: 100%;
  margin: 0.5em;
}
